form {
    width: 60%;
    display: block;
    margin: auto;
}
form input {
    height: 40px;
    width: 100%;
    margin-bottom: 30px;
    
}


form button {
    height: 40px;
    width: 140px;
    margin-top: 20px;
    cursor: pointer;
    background: #201f1f;
    color: #fff;
    border-radius: 5px;
    outline: 1px solid #000;
    border: none;
}

.thanks {
    color: green;
    font-weight: 600;
    display: flex;
    text-align: center;
}
.hide {
    display: none;
}