.summary {
    /* padding: 30px; */

}

.summary-movie-name {
    font-size: 60px;
    color: #fff;
}

.summary-movie {
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(180deg, rgb(0, 0, 0), rgba(0, 0, 0, 0.763), #010101);
    padding: 20px;
    gap: 50px;
}

.summary-img img {
    height: 460px;
    width: 330px;
    border-radius: 8px;
    box-shadow: 0px 0px 10px skyblue;
}

.movie-info {
    display: flex;
    flex-direction: column;
}

.rate-span-summary {
    color: #fff;
    display: flex;
    align-items: center;
    gap: 10px;
}

.genres-summary {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
    color: #fff;
}

.genres-summary .book-tickets {
    padding: 15px;
    width: 150px;
    text-decoration: none;
    text-align: center;
    margin-top: 20px;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    background: #2c2a2a;
    color: #fff;
    outline: 1px solid #fff;
}

h1 {
    font-weight: 600;
}

.summary .details {
    padding: 20px;
}

@media (max-width:600px) {
    .summary-movie {
        flex-direction: column;
    }
    .summary-img img {
        width: 100%;
        height: 100%;
    }
}