.movies-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 40px;
    gap: 40px;
}

.movie-name {
    font-weight: 600;
    font-size: 18px;
   
}

.movie-img {
    height: 350px;
    width: 250px;
    border-radius: 5px;
}

.movies-item {
    height: 350px;
    width: 250px;
    margin-bottom: 60px;
    position: relative;
}

.rating-icon {
    color: rgb(255, 187, 110);
    margin-left: 10px;
}

.rate-span {
    display: flex;
    align-items: center;
    gap: 10px;
}

.movie-item-footer {
    background: rgb(51, 53, 69);
    width: 100%;
    color: #fff;
    position: absolute;
    bottom: 0%;
    height: 40px;
    display: flex;
    align-items: center;
}
.movie-name-genres {
    line-height: 10px;
    margin-top: 20px;
}